import React from "react";
import './index.css';

const { Provider, Consumer } = React.createContext()

// 测试 createContext, 爷->孙 传值
export default class TestContext extends React.Component {
  render(){
    const toGrandObj = {
      aaa: '111',
      mode: 'dark'
    }

    return(
      <Provider value={toGrandObj}>
        <div className="test-context-wrap">
          TestContext
          <Son></Son>
        </div>
      </Provider>
      
    )
  }
}

function Son(props){
  return (
    <div>
      Son
      <GrandSon></GrandSon>
    </div>
  )
}

function GrandSon(props){
  return (
    <Consumer>
      { val => (
        <div {...props}>GrandSon mode: {val.mode}</div>
      ) }
    </Consumer>
  )
}