import { Component } from "react";

export default class MyForm extends Component{
  constructor(props){
    super(props);

    this.state = {
      value: '',
      selectVal: '4'
    }
    this.handleSubmit = this.handleSubmit.bind(this);
    this.valueChange = this.valueChange.bind(this);
    this.selectChange = this.selectChange.bind(this);
    this.inputChange = this.inputChange.bind(this);
  }

  async valueChange(e){
    // console.log('valueChange-e:', e.target.name)
    const value = e.target.value
    await this.setState({
      value: value,
    })
    // console.log(this.state)
  }
  
  async selectChange(e){
    // console.log('selectChange-e:', e)

    const value = e.target.value
    await this.setState({
      selectVal: value,
    })
    // console.log(this.state)

  }
  inputChange(e){
    const target = e.target;
    this.setState({
      [target.name]: target.value
    })
  }

  handleSubmit(e){
    alert(`name: ${this.state.value}`)
    e.preventDefault();
  }

  render(){
    return (
      <div>
        <h3>myForm</h3>
        <form onSubmit={this.handleSubmit}>
          <input name="value" type="text" value={this.state.value} onChange={this.inputChange} />
          <select name="selectVal" value={this.state.selectVal} onChange={this.inputChange}>
            <option value="1">测试1</option>
            <option value="2">测试2</option>
            <option value="3">测试3</option>
            <option value="4">测试4</option>
          </select>
          <input type="submit" />
        </form>
      </div>
      
    )
  }
}