import './index.css';

import { Component } from 'react';
import Square from '../square/Square';

export default class Board extends Component {

  getSquare(i){
    return <Square val={this.props.squreVals[i]} onClick={()=> this.props.onClick(i)}></Square>
  }

  render(){

    return (
      <div className='board-warp'>
        <div className='board-warp-row'>
          {this.getSquare(0)}
          {this.getSquare(1)}
          {this.getSquare(2)}
        </div>
        <div className='board-warp-row'>
          {this.getSquare(3)}
          {this.getSquare(4)}
          {this.getSquare(5)}
        </div>
        <div className='board-warp-row'>
          {this.getSquare(6)}
          {this.getSquare(7)}
          {this.getSquare(8)}
        </div>
      </div>
    )
  }
}