import { Component } from "react";
import { products } from './helper/data.js';

// 商品列表组件
export default class ShopList extends Component {
  constructor(props){
    super(props);
    this.state = {
      searchVal: '',
      tableData: products,
      isStocked: false
    }
    this.searchChange = this.searchChange.bind(this);
    this.stockedChange = this.stockedChange.bind(this);
    this.getHandledData = this.getHandledData.bind(this);
  }
  // 监听input change
  searchChange(e){
    this.setState({
      searchVal: e.target.value
    })
  }
  // 监听checkbox change
  stockedChange(e){
    this.setState({
      isStocked: e.target.checked
    })
  }
  // 处理列表数据 搜索筛选
  getHandledData(){
    const { tableData, searchVal, isStocked } = this.state;
    const newList = tableData.filter(item=>{
      const { category, price, name } = item;
      if(category.includes(searchVal) || price.includes(searchVal) || name.includes(searchVal)){
        if(isStocked){
          if(item.stocked) return true;
          return false
        }else{
          return true
        }
      }else{
        return false
      }
    })
    return newList;
  }
  render(){
    const handledTableData = this.getHandledData()

    return (
      <div>
        <h3>ShopList</h3>
        <Search value={this.state.searchVal} onChange={this.searchChange}></Search>
        <FilterBox value={this.state.isStocked} onChange={this.stockedChange}></FilterBox>
        <GoodsTable products={handledTableData}></GoodsTable>
      </div>
    )
  }
}

// 搜索
function Search(props){
  return (
    <input type="text" placeholder="search..." value={props.value} onChange={props.onChange} />
  )
}

// 筛选是否有存储
function FilterBox(props){
  return (
    <div>
      <label htmlFor="checkBoxId">
        <input id="checkBoxId" type="checkbox" value={props.value} onChange={props.onChange} />
        only show products in stock
      </label>
    </div>
  )
}

// 商品表格
class GoodsTable extends Component {

  getGoodsBlocks(originList){
    let typesArr = [];
    let newList = [];
    originList.forEach(item=>{
      const category = item.category
      if(typesArr.includes(category)){
        newList[typesArr.indexOf(category)].push(item)
      }else{
        typesArr.push(category);
        newList[typesArr.indexOf(category)] = [item]
      }
    })
    return (
      newList.map((item,index)=>(
        <GoodsBlock typeName={item[0].category} goodsList={item} key={index}></GoodsBlock>
      ))
    )
  }

  render(){
    const goodsBlocks = this.getGoodsBlocks(this.props.products);

    return (
      <table>
        <thead>
          <tr>
            <th>Name</th>
            <th>Price</th>
          </tr>
        </thead>
        <tbody>
          { goodsBlocks }
        </tbody>
        
      </table>
    )
  }
}

// 商品类型
class GoodsType extends Component {
  render(){
    return (
      <tr>
        <td 
          colSpan="2" 
          style={{textAlign: 'center',textDecoration: 'underline'}}
        >
          {this.props.typeName || '1'}
        </td>
      </tr>
    )
  }
}

// 商品行
class GoodsRow extends Component {
  render(){
    const { name, price, stocked } = this.props;

    return (
      <tr>
        <td style={{color:stocked?'red':'#000'}}>{name}</td>
        <td>{price}</td>
      </tr>
    )
  }
}

// 商品块 商品类型-商品行-商品行...
class GoodsBlock extends Component {
  render(){
    const { goodsList, typeName } = this.props;
    return (
      <>
        <GoodsType typeName={typeName}></GoodsType>
        { goodsList.map((item,index)=>(
          <GoodsRow name={item.name} price={item.price} stocked={item.stocked} key={index}></GoodsRow>
        )) }
      </>
    )
  }
}