import './index.css';

import { Component } from "react";
import Board from "../board/Index";

export default class Game extends Component{
  constructor(props){
    super(props);

    this.state = {
      squreVals: Array(9).fill(null),
      stepNum: 0, // 0是第一步
      history: []
    }
    
  }
  
  // 点击方块
  handleClickSquare(index){
    let squreValsCopied = this.state.squreVals.slice();
    if(this.hadWinner(this.state.squreVals)) return;
    if(squreValsCopied[index]) return;
    squreValsCopied[index] = this.getXIsNext(this.state.stepNum) ? 'x' : 'o';
    
    let nowStepNum = this.state.stepNum + 1;
    let newHistory = this.state.history.slice();
    newHistory.push(squreValsCopied);
    this.setState({
      squreVals: squreValsCopied,
      stepNum: nowStepNum,
      history: newHistory
    })
    // console.log('handleClickSquare', index )
  }

  // 获取提示文案
  getTipTxt(){
    if(this.hadWinner(this.state.squreVals)){
      return '出现赢家'
    }else{
      return `下一步是${this.getXIsNext(this.state.stepNum)?'x':'o'}`
    }
  }

  // 历史记录
  HistoryList(){
    return (
      <ol>
        {this.state.history.map((item, index)=>(
          <li 
            key={index}
            onClick={()=>this.goHistory(index)}
          >
            #{index%2===0 ? 'x' : 'o'}
          </li>
        ))}
      </ol>
    )
  }

  getXIsNext(num){
    return num%2===0? true : false;
  }

  // 回到某个历史
  goHistory(i){
    let historyOne = this.state.history[i];
    let currentHistory = this.state.history.slice(0, i+1);
    console.log('currentHistory:', currentHistory)
    this.setState({
      squreVals: historyOne,
      stepNum: i+1,
      history: currentHistory
    })
  }

  hadWinner(nowList){
    const winList = [
      [0,1,2],
      [3,4,5],
      [6,7,8],
      [0,3,6],
      [1,4,7],
      [2,5,8],
      [0,4,8],
      [2,4,6]
    ]
    for(let arr of winList){
      if(nowList[arr[0]] === 'x' && nowList[arr[0]]===nowList[arr[1]] && nowList[arr[0]]===nowList[arr[2]]){
        return true
      }
      if(nowList[arr[0]] === 'o' && nowList[arr[0]]===nowList[arr[1]] && nowList[arr[0]]===nowList[arr[2]]){
        return true
      }
    }
    return false
  }

  // 点击 重新开始 按钮
  handleResetBtn(){
    this.setState({
      squreVals: Array(9).fill(null),
      stepNum: 0,
      history: []
    })
  }

  render(){

    return (
      <div>
        <div>{this.getTipTxt()}</div>
        <Board 
          squreVals={this.state.squreVals}
          onClick={(e)=>this.handleClickSquare(e)}
        ></Board>
        <button className="resetBtn" onClick={()=>this.handleResetBtn()}>重新开始</button>
        <div className="history-title">历史记录</div>
        {this.HistoryList()}
      </div>
    )
  }
}