import './index.css';

import { Component } from "react";

export default class MoneyCalculator extends Component {
  constructor(props){
    super(props);
    this.state = {
      moneyNum: '',
      type: 'm' // m=美元 r=人名币
    }
    this.moneyChange = this.moneyChange.bind(this);
  }

  moneyChange(e, type){
    // console.log('moneyChange-e:', e)
    this.setState({
      moneyNum: e.target.value,
      type: type
    })
  }
  calcMoney(num, toType){
    const parsedNum = parseFloat(num);
    if(isNaN(parsedNum)) return '';
    if(toType==='m'){ // 人 => 美
      return (parsedNum*0.1364).toFixed(4)
    }
    if(toType==='r'){ // 美 => 人
      return (parsedNum*7.3319).toFixed(4)
    }
  }

  render(){
    const moneyNum = this.state.moneyNum;
    const type = this.state.type;
    const mNum = type==='m' ? moneyNum : this.calcMoney(moneyNum, 'm');
    const rNum = type==='r' ? moneyNum : this.calcMoney(moneyNum, 'r');

    return (
      <div className="money-calculator-wrap">
        <h3>MoneyCalculator</h3>
        <MoneyInput 
          label="美元" 
          value={mNum} 
          onChange={(e)=>this.moneyChange(e, 'm')}
        ></MoneyInput>
        <MoneyInput 
          label="人名币" 
          value={rNum} 
          onChange={(e)=>this.moneyChange(e, 'r')}
        ></MoneyInput>
      </div>
    )
  }
}

function MoneyInput(props){
  return (
    <div>
      <label htmlFor="">
        { props.label }
        <input type="text" value={props.value} onChange={props.onChange} />
      </label>
    </div>
  )
}

