import './index.css';

import { Component } from "react";
import Warning from '../warning/index';

export default class Toogle extends Component {
  constructor(props){
    super(props);
    this.state = {
      isToogleOn: true
    }
    this.clickBtn = this.clickBtn.bind(this) // 构造函数中绑定this的正确指向
  }

  clickBtn(){
    // console.log('this:', this)
    this.setState((prevState)=>({
      isToogleOn: !prevState.isToogleOn
    }))
  }

  render(){
    const isToogleOn = this.state.isToogleOn;

    return (
      <div>
        <button className="toogle-btn" onClick={this.clickBtn}>
          { this.state.isToogleOn ? 'on' : 'off' }
        </button>
        <Warning warn={isToogleOn}></Warning>
      </div>
      
    )
  }
}